	module.exports = {"data":{"post":{"html":"<p>I love reading bloggers like Tim Bray who can make connections between seemingly disconnected fields, in his case tech and politics. Here's how he experienced the <a href=\"http://www.tbray.org/ongoing/When/200x/2009/01/21/Inauguration\">inauguration</a> in a hotel lobby in San Jose, where the entire room broke in to applause (it's a short entry, I'm quoting it nearly entirely):</p>\n<blockquote><p>... in Silicon Valley, the entrepreneurs and engineers, while mostly men, are a mixed bag ethnically, and their language is English. But the people who work the reception desks and wash the floors and bring the coffee and provide security, their skins are all black and brown (no whites); they have heavy accents and talk lots of different languages, some I can&rsquo;t even identify, to each other. It&rsquo;s been that way long as I can remember. This apparently-fixed class/race background is one of the things that bothers me about the Valley.</p></blockquote>\n<blockquote><p>I think the applause was maybe a little louder from the (standing) hotel employees than the (sitting) business breakfasters.</p></blockquote>","fields":{"title":"Male and Pale","date":"21 Jan 2009","url":"https://papascott.de/2009-01-21-male-and-pale/"}}},"pathContext":{"slug":"/2009-01-21-male-and-pale/","prev":{"fields":{"title":"Ice Fishing","date":"07 Jan 2009","slug":"/2009-01-07-ice-fishing/"}},"next":{"fields":{"title":"McCredit Card","date":"22 Jan 2009","slug":"/2009-01-22-mccredit-card/"}}}}

